@import url(https://fonts.googleapis.com/css?family=Open+Sans:600,300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

#navigation {
  display: flex
}
a {
  text-decoration: none;
}
#main {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.profile{width:200px;height:320px;text-align:center;padding:15px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);margin:5px}.profile__image{position:relative;width:70px;height:70px;margin:38px auto 0 auto}.profile__image .circle-1,.profile__image .circle-2{position:absolute;box-sizing:border-box;width:76px;height:76px;top:-3px;left:-3px;border-width:1px;border-style:solid;border-color:#786450 #786450 #786450 transparent;border-radius:50%;transition:all 1.5s ease-in-out}.profile__image .circle-2{width:82px;height:82px;top:-6px;left:-6px;border-color:#786450 transparent #786450 #786450}.profile__image img{display:block;border-radius:50%;background:#F5E8DF;object-fit:cover;width:100%}.profile__image:hover{cursor:pointer}.profile__image:hover .circle-1,.profile__image:hover .circle-2{transform:rotate(360deg)}.profile__image:hover .circle-2{transform:rotate(-360deg)}.profile__name{font-size:15px;font-weight:600;margin-top:15px}.profile__info{font-size:11px;line-height:10px;padding:2px}.profile__about{font-size:13px;overflow:hidden;max-height:100px}

